.zone {
    width: 80%;
    margin: 1.5% auto;
}

.zone .card-body, .completed .btn {
    width: 100%;
}

.zone .card-title {
    position: relative;
    width: 80%;
}

.zone .card-text {
    position: relative;
    width: 100%;
}

.zone a {
    position: relative;
    width: 20%;
}

.zone .completed, .zone .locked {
    display: inline-flex;
}

.locked .btn {
    width: 20%;
}